<template>
    <div class="files-box">
        <div class="operate-line">
            <div class="operate-box" @click="addPf">
                <svg-loader class="pfIcon" icon="AddTwo" size="2.5rem"></svg-loader>
                <br>
                <span>{{ $t('profile.add') }}</span>
            </div>
            <div>
                <span class="line"></span>
            </div>
            <div :class="[notAllowActivate ? 'not-allow-activate' : 'operate-box']" @click="activatePf">
                <svg-loader class="pfIcon" icon="RightOne" size="2.5rem"></svg-loader>
                <br>
                <span>{{ $t('profile.activate') }}</span>
            </div>
            <div>
                <span class="line"></span>
            </div>
            <div class="operate-box" @click="editPf">
                <svg-loader class="pfIcon" icon="EditTwo" size="2.5rem"></svg-loader>
                <br>
                <span>{{ $t('profile.editor') }}</span>
            </div>
            <div>
                <span class="line"></span>
            </div>
            <div class="operate-box delete-box" @click="delPf">
                <svg-loader class="pfIcon" icon="TrashOne" size="2.5rem"></svg-loader>
                <br>
                <span>{{ $t('profile.delete') }}</span>
            </div>
        </div>
        <div class="items-box">
            <div v-for="i of pfList.length" :key="i" class="profile-item" @click="selectFile(i - 1)">
                <svg-loader :class="{ 'file-static-icon': true, 'file-active-icon': active_item == i - 1 }"
                    :icon="active_item == i - 1 ? 'ActivePf' : 'StaticPf'"></svg-loader>
                <span :class="{ 'pf-static-num': true, 'pf-active-num': active_item == i - 1 }">{{ getPfNum(i) }}</span>
                <span class="file-name">{{ pfList[i - 1]['name'] }}</span>
            </div>
            <div class="profile-item add-box">
                <LeetxButton :module="module" @click="addPf" class="file-static-icon" borderRadius="0" :use-icon="true"
                    icon="AddTwo" bgcColor="transparent" iconSize="2.5rem"></LeetxButton>
            </div>
        </div>
    </div>
</template>

<script>
import { pfDelApi, pfAddApi, pfCurSelApi } from '@/api/profile/profile'
import { messageApi } from '@/components/message/message.js'
import cnfrmWin from '@/dialogs/common/confirm/cnfrm'
import addPsetDialog from '@/dialogs/program/createpro'
// import { devCfgCtrlSrcGetApi } from '../../api/device/device'
import LeetxButton from '@/components/controls/LeetxButton.vue'

export default {
    components: {
        LeetxButton
    },
    data() {
        return {
            module: "PROFILE",
            pfList: [],
            active_item: -1,
        }
    },
    methods: {
        // 展示pset号
        getPfNum(i) {
            let pset = this.pfList[i - 1].pset
            return (pset < 10 ? `0${pset}` : (pset < 10 ? `0${pset}` : `${pset}`))
        },
        // 向工艺编辑区发送信号,删除的文件pset,
        delPf() {
            if (this.$store.state.f_status.user == 'guest') {
                return messageApi.warning(['guest用户无权限操作'])
            }
            if (this.active_item == -1) return
            let pset = this.pfList[this.active_item].pset
            if (pset == this.activatePset) {
                let msg = this.$t('promsg.no_delete')
                messageApi.warning([msg])
                return
            }
            let msg = `${this.$t('promsg.is_del')}${this.pfList[this.active_item].pset}`
            console.log('this.$route.path', this.$route.path);
            console.log('this.$route.query', this.$route.query);
            if (this.$route.path.startsWith("/profile") && this.$route.query && this.$route.query.pset == pset) {
                msg = this.$t('promsg.deleteFileEditing')
            }
            cnfrmWin({ title: this.$t('promsg.del'), confirmText: this.$t('promsg.yes'), cancelText: this.$t('promsg.no'), msg }).then(() => {
                pfDelApi({ 'pset': this.pfList[this.active_item].pset }).then((res) => {
                    if (res.status == 9130) {
                        messageApi.error(["删除失败"])
                        return
                    }
                    if (res.status == 9125) {
                        messageApi.warning(['文件不存在或已经删除'])
                        return
                    }
                    if (res.status != 0) {
                        messageApi.warning([this.$t('promsg.nexit'), this.$t('promsg.has_del')])
                        return
                    }
                    if (this.$route.path.startsWith("/profile") && this.$route.query && this.$route.query.pset == pset) {
                        this.$router.push({ path: '/profile', query: { 'pset': -1 } })
                    }
                    this.$emit('operate', { action: "sub" })
                    messageApi.success([this.$t('promsg.del_suc')])

                }).catch(() => {
                    messageApi.error(["删除失败"])
                    this.$emit('operate', { action: "sub" })
                });
                this.active_item = -1
            })
        },
        // 添加文件
        addPf() {
            addPsetDialog({ "pfList": this.pfList }).then((result) => {
                let pset = result.pset
                pfAddApi(result.Profile).then((res) => {
                    if (res.status == 9121) {
                        messageApi.warning(['工艺号或工艺名称重复'])
                        return
                    }
                    else if (res.status == 9123) {
                        messageApi.warning(['文件写入数据库失败'])
                        return
                    }
                    else if (res.status == 0) {
                        this.$router.push({ path: '/profile', query: { 'pset': pset } })
                    } else {
                        messageApi.error([this.$t('promsg.add_fail')])
                        this.$router.push({ path: '/profile', query: { 'pset': pset } })
                    }
                    this.$emit('operate', { action: "add" })
                }).catch(() => {
                    messageApi.error([this.$t('promsg.add_fail')])
                    this.$emit('operate', { action: "add" })
                });
            })
        },
        // 排序
        sortBy(field) {
            return function (a, b) {
                return a[field] - b[field];
            }
        },
        // 编辑文件
        editPf() {
            if (this.active_item == -1) { return }
            let pset = this.pfList[this.active_item].pset
            this.$router.push({ path: '/profile', query: { 'pset': pset } })
            console.log({ path: '/profile', query: { 'pset': pset } })
            this.$emit('operate', { action: "edit" })
        },
        // 激活文件
        activatePf() {
            if (this.$store.state.g_status.psetsrc != 0x01) {
                let msgOne = this.$t('promsg.msgOne')
                let msgTwo = this.$t('promsg.msgTwo')
                messageApi.warning([msgOne, msgTwo])
                return
            }
            if (this.active_item == -1) {
                messageApi.error([this.$t('promsg.nselect_file')])
                return
            }
            if (this.pfList[this.active_item].pset == this.$store.state.g_status.pset) {
                messageApi.warning([this.$t('promsg.file_locate')])
                return
            }
            if (this.$store.state.g_status.busy) {
                messageApi.warning([this.$t('promsg.run_status'), this.$t('promsg.nactivate_file')])
                return
            }
            let pset = this.pfList[this.active_item].pset
            pfCurSelApi(pset).then((result) => {
                if (result.status == 9126 || result.status == 9129) {
                    messageApi.warning(['下位机切换文件失败'])
                    return
                }
                if (result.status == 0) {
                    messageApi.success([this.$t('promsg.activate_suc')])
                } else {
                    messageApi.error("激活失败!")
                }
                this.$emit('operate', { action: "activate" })

            }).catch(() => {
                messageApi.error([this.$t('promsg.activate_fail')])
                this.$emit('operate', { action: "activate" })
            });
        },
        selectFile(index) {
            this.active_item = index == this.active_item ? -1 : index
        },
        updateActiveItem() {
            this.$bus.on('highlightActiveFile', (data) => {
                let activeIndex = -1
                if (data.pset == 0) {
                    activeIndex = -1
                }
                else {
                    for (let i = 0; i < this.pfList.length; i++) {
                        if (this.pfList[i].pset == data.pset) {
                            activeIndex = i
                            break
                        }
                    }
                }
                this.active_item = activeIndex
            })
        },
        closeUpdateActiveItem() {
            this.$bus.off('highlightActiveFile')
        },

    },
    props: {
        fileItems: {
            type: Array,
            default: function () {
                return []
            }
        },
    },
    watch: {
        fileItems: {
            immediate: true,
            deep: true,
            handler: function () {
                this.pfList = this.fileItems
                this.pfList.sort(this.sortBy('pset'))
            }
        },

    },
    created() {
        this.updateActiveItem()
        // this.getControlData()
    },
    unmounted() {
        // this.closeUpdateActiveItemo()
    },
    computed: {
        notAllowActivate() {
            return this.$store.state.g_status.psetsrc != 0x01
        },
        // editingPset() {
        //     return this.$store.state.f_status.editingPset
        // },
        activatePset() {
            return this.$store.state.g_status.pset
        },
        enabled() {
            if (this.module in this.$store.state.f_status.permissions && this.$store.state.f_status.permissions[this.module] == 4)
                return true
            else
                return false
        },
        pointerEvent() {
            return this.enabled ? "auto" : "none";
        },
        opacity() {
            return this.enabled ? 1 : 0.3;
        },
    }
}
</script>
<style scoped>
.files-box {
    position: relative;
    width: 100vw;
    background-color: var(--Cee);
    min-height: 20rem;
    z-index: 36 !important;
}

.operate-line {
    position: relative;
    top: 0.5rem;
    width: 100vw;
    display: flex;
    box-sizing: border-box;
    border-bottom: 1px solid var(--CD4);
    pointer-events: v-bind(pointerEvent);
    opacity: v-bind(opacity);
}

.operate-line div {
    flex: 1;
    padding: 5px;
    position: relative;
}

.line {
    display: block;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 2.05rem;
    width: 1px;
    background-color: var(--CD4);
}

.operate-line .operate-box {
    flex: 3;
    font-size: var(--font1_625);
    font-weight: 500;
    color: #3B3B3B;
}

.operate-box:active,
.operate-box:active .pfIcon {
    color: var(--C00c155) !important;
}

.delete-box:active,
.delete-box:active .pfIcon {
    color: var(--CE61F00) !important;
}

.items-box {
    position: relative;
    width: 100vw;
    padding: 1px 0;
}

.profile-item {
    position: relative;
    float: left;
    margin: 2.2rem;
    list-style: none;
    width: 3.891rem;
    height: 4.4465rem;
    cursor: pointer;
}

.file-static-icon {
    position: absolute;
    width: 2.625rem;
    height: 3rem;
    left: 50%;
    transform: translate(-50%, -50%);
    top: 50%;
    font-size: var(--font1_625);
}

.file-active-icon {
    position: absolute;
    width: 3.875rem;
    height: 4.4375rem;
    left: 50%;
    transform: translate(-50%, -50%);
    top: 50%;
    font-size: 2.4rem;
}

.pf-static-num {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: var(--font1_625);
}

.pf-active-num {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 2.4rem;
}

.file-name {
    display: inline-block;
    font-family: var(--SHSC-R);
    font-size: 1.5rem;
    font-weight: normal;
    width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
    color: var(--C3B);
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 100%;
}

.not-allow-activate {
    font-size: var(--font1_625);
    font-weight: 500;
    color: var(--Cb0) !important;
}

.operate-box .not-allow-activate:active {
    font-size: var(--font1_625);
    font-weight: 500;
    color: var(--Cb0) !important;

}
</style>